
Template.__checkName("MeteorToys_pubsub");
Template["MeteorToys_pubsub"] = new Template("Template.MeteorToys_pubsub", (function() {
  var view = this;
  return Blaze._TemplateWith(function() {
    return {
      name: Spacebars.call("MeteorToys_pubsub")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("MeteorToy"), function() {
      return [ "\n		", Blaze.Unless(function() {
        return Spacebars.call(view.lookup("editing"));
      }, function() {
        return [ "\n			", Spacebars.include(view.lookupTemplate("MeteorToys_pubsub_header")), "\n			", Spacebars.include(view.lookupTemplate("MeteorToys_pubsub_content")), "\n		" ];
      }, function() {
        return [ "\n			", Spacebars.include(view.lookupTemplate("MeteorToys_pubsub_new_header")), "\n			", Spacebars.include(view.lookupTemplate("MeteorToys_pubsub_new")), "\n		" ];
      }), "\n	" ];
    });
  });
}));

Template.__checkName("MeteorToys_pubsub_header");
Template["MeteorToys_pubsub_header"] = new Template("Template.MeteorToys_pubsub_header", (function() {
  var view = this;
  return HTML.Raw('<div class="MeteorToys_pubsub_header MeteorToys-background-overlay1">\n		<!-- <div class="MeteorToys_pubsub_button">New</div> -->\n		<div class="MeteorToys_name"><strong>Subscriptions</strong></div>\n	</div>');
}));

Template.__checkName("MeteorToys_pubsub_new_header");
Template["MeteorToys_pubsub_new_header"] = new Template("Template.MeteorToys_pubsub_new_header", (function() {
  var view = this;
  return HTML.Raw('<div class="MeteorToys_pubsub_header">\n		<div class="MeteorToys_pubsub_button">Cancel</div>\n		<div class="MeteorToys_name"><strong>Create New Subscription</strong></div>\n	</div>');
}));

Template.__checkName("MeteorToys_pubsub_content");
Template["MeteorToys_pubsub_content"] = new Template("Template.MeteorToys_pubsub_content", (function() {
  var view = this;
  return HTML.DIV({
    "class": "MeteorToys_pubsub_content"
  }, "\n		", Blaze.Each(function() {
    return Spacebars.call(view.lookup("subscription"));
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "MeteorToys_row "
    }, "\n				", HTML.DIV({
      "class": function() {
        return [ "MeteorToys_pubsub_row_toggle MeteorToys_pubsub_row_toggle_", Spacebars.mustache(view.lookup("name")) ];
      }
    }, HTML.CharRef({
      html: "&times;",
      str: "Ã—"
    })), "\n				", HTML.DIV({
      "class": "MeteorToys_pubsub_row_name"
    }, Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    })), "\n				", Blaze.If(function() {
      return Spacebars.call(view.lookup("parameters"));
    }, function() {
      return [ "\n					Params: ", Blaze.View("lookup:parameters", function() {
        return Spacebars.mustache(view.lookup("parameters"));
      }), "\n				" ];
    }, function() {
      return "\n					No Parameters\n				";
    }), "\n			"), "\n		" ];
  }, function() {
    return [ "\n			", HTML.DIV({
      style: "padding-top: 4px"
    }, "No subscriptions available"), "\n		" ];
  }), "\n	");
}));

Template.__checkName("MeteorToys_pubsub_new");
Template["MeteorToys_pubsub_new"] = new Template("Template.MeteorToys_pubsub_new", (function() {
  var view = this;
  return HTML.DIV({
    "class": "MeteorToys_pubsub_content"
  }, "\n		", Blaze.Unless(function() {
    return Spacebars.call(view.lookup("newTarget"));
  }, function() {
    return [ "\n			", Blaze.Each(function() {
      return Spacebars.call(view.lookup("sub"));
    }, function() {
      return [ "\n				", HTML.DIV({
        "class": "MeteorToys_row MeteorToys_row_sub"
      }, "\n					", Blaze.View("lookup:.", function() {
        return Spacebars.mustache(view.lookup("."));
      }), HTML.CharRef({
        html: "&nbsp;",
        str: "Â "
      }), "\n				"), "\n			" ];
    }), "\n		" ];
  }, function() {
    return [ "\n			", Spacebars.With(function() {
      return Spacebars.call(view.lookup("newTarget"));
    }, function() {
      return [ "\n				", HTML.DIV({
        "class": "MeteorToys_row"
      }, "\n					Subscription: ", Blaze.View("lookup:name", function() {
        return Spacebars.mustache(view.lookup("name"));
      }), "\n				"), "\n				", HTML.FORM("\n					", Blaze.Each(function() {
        return Spacebars.call(view.lookup("params"));
      }, function() {
        return [ "\n						", HTML.DIV({
          "class": "MeteorToys_row"
        }, "\n							", Blaze.View("lookup:.", function() {
          return Spacebars.mustache(view.lookup("."));
        }), "\n							", HTML.INPUT({
          id: function() {
            return [ "MeteorToys_pubsub_param_", Spacebars.mustache(view.lookup(".")) ];
          },
          tabindex: "-1"
        }), "\n						"), "\n					" ];
      }), "\n					", HTML.INPUT({
        type: "submit",
        value: "Start Subscription"
      }), "\n				"), "\n			" ];
    }), "\n		" ];
  }), "\n	");
}));
